.widgetLg
{
    flex: 2;
    box-shadow: 2px 2px 10px 1px rgba(102,99,99,0.38);
    -webkit-box-shadow: 2px 2px 10px 1px rgba(102,99,99,0.38);
    -moz-box-shadow: 2px 2px 10px 1px rgba(102,99,99,0.38);
    padding: 20px;
}

.WidgetLgTitle
{
    font-weight: 22px;
    font-weight: 600;
}
.widgetLgTable
{
    width: 100%;
    border-spacing: 20px;
}
.widgetLgTh
{
    text-align: left;
}
.widgetLgUser
{
    display: flex;
    align-items: center;
    font-weight: 600;
}
.widgetLgImg
{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.widgetLgDate,.widgetLgAmount
{
    font-weight: 300;
}
.widgetLgButton
{
    padding: 7px 7px;
    border:none;
    border-radius: 10px;
}
.widgetLgButton.Approved
{
    background-color: #e5faf2;
    color: #3bb077;
}
.widgetLgButton.Declined
{
    background-color: #fff0f1;
    color: #d95087;
}
.widgetLgButton.Pending
{
background-color: #ebf1fe;
color: #2a7ade;
}